<!--
 * @Descripttion: 机构维护列表
 * @Author: 曾元球
 * @Date: 2019-05-30 10:31:37
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-12 13:12:09
 -->

<template>
  <div class='page flex-v'>
    <div class='head flex-v-center flex-justify'>
      <div class='head-title'>机构维护</div>
      <div class='head-btn'
        v-if='btns.hasInsert'
        data-btn-id='106001001'
        @click='openDialog("insert")'>
        <img src='@/assets/img/add.png'
          alt='新增'>&nbsp;<span>新增</span>
      </div>
    </div>
    <div class='query-form'>
      <el-form inline
        size='small'
        class='default-form-style'>
        <el-form-item label='机构编码：'>
          <el-input placeholder='请输入'
            v-model='querys.sysGuid'
            clearable />
        </el-form-item>
        <el-form-item label='上级机构名称：'>
          <el-input placeholder='请输入'
            v-model='querys.parentName'
            clearable />
        </el-form-item>
        <el-form-item label='机构名称：'>
          <el-input placeholder='请输入'
            v-model='querys.oname'
            clearable />
        </el-form-item>
        <el-form-item>
          <el-button icon='el-icon-search'
            @click='$refs.pagination.initPageList(1)'
            round>查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class='table-box flex-item'>
      <el-table :data='tableData'
        height='100%'
        header-cell-class-name='custom-table-cell'
        v-loading='loading'
        stripe>
        <el-table-column label='序号'
          type='index'>
          <template v-slot='{$index}'>
            {{$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column label='机构编号'
          prop='sysGuid'
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column label='机构名称'
          prop='oname'></el-table-column>
        <el-table-column label='上级机构名称'
          prop='parentName'
          show-overflow-tooltip>
          <template v-slot='{row}'>
            {{row.parentName | emptyData}}
          </template>
        </el-table-column>
        <el-table-column label='机构类别'
          prop='otype'>
          <template v-slot='{row}'>{{orgTypes[row.otype - 1]}}</template>
        </el-table-column>
        <el-table-column label='状态'
          width='120'>
          <template v-slot='{row}'>
            <span :class="[row.enabled == 1 ? 'status-enable' : 'status-disable']"></span>
          </template>
        </el-table-column>
        <el-table-column label='操作'
          width='120'>
          <template v-slot='{row}'>
            <i class='iconfont bj-search'
              @click='openDialog("see", row)'></i>
            <i class='iconfont bj-edit hand-shape'
              v-if='btns.hasUpdate&&row.enabled==1'
              data-btn-id='106001002'
              @click='openDialog("update", row)'></i>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class='foot'>
      <bj-pagination query-url='/sys/org/listPaging'
        :query-obj='querys'
        @listResult='getListResult'
        @controlLoading='loading=$event'
        ref='pagination' />
    </div>
    <el-dialog :visible.sync='dialogIsShow'
      custom-class='my-dialog'
      class='flex'
      width='500px'
      :title='dialogTitles[dialogType]'
      :close-on-click-modal='false'
      center>
      <template v-if='dialogType==="see"'>
        <div class='flex'>
          <el-form size='small'
            class='see-style'
            label-width='auto'>
            <el-form-item label='机构编码：'>
              {{dataInfo.sysGuid}}
            </el-form-item>
            <el-form-item label='机构名称：'>
              {{dataInfo.oname}}
            </el-form-item>
            <el-form-item label='上级机构：'>
              {{dataInfo.parentName | emptyData}}
            </el-form-item>
            <el-form-item label='所属区域：'>
              {{regionAddress | emptyData}}
            </el-form-item>
            <el-form-item label='机构类别：'>
              {{orgTypes[dataInfo.otype - 1]}}
            </el-form-item>
            <el-form-item label='机构地址：'>
              {{dataInfo.orgAddress | emptyData}}
            </el-form-item>
            <el-form-item label='联系人：'>
              {{dataInfo.contacts | emptyData}}
            </el-form-item>
            <el-form-item label='联系电话：'>
              {{dataInfo.contactsMobile | emptyData}}
            </el-form-item>
            <el-form-item label='状态：'>
              {{dataInfo.enabled == 1 ? '启用' : '禁用'}}
            </el-form-item>
          </el-form>
        </div>
        <div slot='footer'
          v-if='btns.hasUpdate'
          data-btn-id='106001002'>
          <el-button class='btn-info'
            @click='openDialog("update")'>
            修改
          </el-button>
        </div>
      </template>
      <template v-else>
        <el-form size='small'
          label-width='auto'
          :model='formFields'
          :rules='rules'
          ref='form'>
          <el-form-item label='机构名称：'
            prop='oname'>
            <el-input placeholder='请输入机构名称'
              v-model='formFields.oname'
              maxlength='31'></el-input>
          </el-form-item>
          <el-form-item label='机构类别：'
            prop='otype'>
            <el-select v-model='formFields.otype'
              class='block'>
              <el-option value='1'
                label='医院'></el-option>
              <el-option value='2'
                label='卫生院'></el-option>
              <el-option value='3'
                label='卫生所'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label='上级机构：'
            prop='parentId'>
            <!-- :disabled='isDisabeldParentOrgSelect' -->
            <el-select v-model='formFields.parentId'
              class='block'>
              <el-option v-for='item in parentOrgList'
                :key='item.sysGuid'
                :value='item.sysGuid'
                :label='item.oname'></el-option>
            </el-select>
          </el-form-item>
          <div class='flex'>
            <el-form-item label=' 所属区域：'></el-form-item>
            <div class='flex flex-item'
              style='flex-wrap: wrap'>
              <el-form-item label-width='0'
                prop='provinceId'>
                <el-select v-model='formFields.provinceId'
                  @change='handleSelect("city", $event)'
                  popper-class='select-width'>
                  <el-option v-for='item in provinceList'
                    :value='item.provinceId'
                    :label='item.provinceName'
                    :key='item.provinceId'></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label-width='0'
                prop='cityId'>
                <el-select v-model='formFields.cityId'
                  @change='handleSelect("county", $event)'>
                  <el-option v-for='item in cityList'
                    :value='item.cityId'
                    :label='item.cityName'
                    :key='item.cityId'></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label-width='0'
                prop='areaId'>
                <el-select v-model='formFields.areaId'
                  @change='handleSelect("town", $event)'>
                  <el-option v-for='item in countyList'
                    :value='item.countyId'
                    :label='item.countyName'
                    :key='item.countyId'></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label-width='0'
                prop='streetId'>
                <el-select v-model='formFields.streetId'
                  @change='handleSelect("village", $event)'>
                  <el-option v-for='item in townList'
                    :value='item.townId'
                    :label='item.townName'
                    :key='item.townId'></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label-width='0'
                prop='communityId'>
                <el-select v-model='formFields.communityId'
                  class='block'>
                  <el-option v-for='item in villageList'
                    :value='item.villageId'
                    :label='item.villageName'
                    :key='item.villageId'></el-option>
                </el-select>
              </el-form-item>
            </div>
          </div>

          <el-form-item label='机构地址：'
            prop='orgAddress'>
            <el-input placeholder='请输入机构地址'
              v-model='formFields.orgAddress'
              maxlength='51'></el-input>
          </el-form-item>
          <el-form-item label='联系人：'
            prop='contacts'>
            <el-input placeholder='请输入联系人'
              v-model='formFields.contacts'
              maxlength='11'></el-input>
          </el-form-item>
          <el-form-item label='联系电话：'
            prop='contactsMobile'>
            <el-input placeholder='请输入联系电话'
              v-model.trim='formFields.contactsMobile'
              maxlength='13'></el-input>
          </el-form-item>
          <el-form-item label='状态：'
            prop='enabled'>
            <el-select v-model='formFields.enabled'
              class='block'
              placeholder='启用'>
              <el-option value='1'
                label='启用'></el-option>
              <el-option value='2'
                label='禁用'></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot='footer'>
          <el-button class='btn-hollowing'
            @click='dialogIsShow = false'>取消</el-button>
          <el-button class='btn-info'
            @click='onSubmit'
            :loading='submitLoading'>保存</el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { lenRule, regPhone } from '@/lib/utils'
const rangeObj = {
  city: {
    url: 'getCity',
    paramKey: 'provinceId',
    feildKey: 'provinceId',
    clearFeilds: ['cityId', 'areaId', 'streetId', 'communityId'],
    clearData: ['countyList', 'townList', 'villageList']
  },
  county: {
    url: 'getCounty',
    paramKey: 'cityId',
    feildKey: 'cityId',
    clearFeilds: ['areaId', 'streetId', 'communityId'],
    clearData: ['townList', 'villageList']
  },
  town: {
    url: 'getTown',
    paramKey: 'countyId',
    feildKey: 'areaId',
    clearFeilds: ['streetId', 'communityId'],
    clearData: ['villageList']
  },
  village: {
    url: 'getVillage',
    paramKey: 'townId',
    feildKey: 'streetId',
    clearFeilds: ['communityId'],
    clearData: []
  }
}
const USE = '1' // 默认为使用状态
export default {
  name: 'AgencyManage',
  data() {
    return {
      // 列表数据
      tableData: [],

      // 上级机构列表
      parentOrgList: [],
      provinceList: [], // 省
      cityList: [], // 市
      countyList: [], // 县
      townList: [], // 镇
      villageList: [], // 村

      // 查询
      querys: {
        oname: '',
        parentName: '',
        sysGuid: ''
      },
      pageNum: 1,
      pageSize: 10,

      // 新增/更新所提交字段
      formFields: {
        sysGuid: '',
        otype: '', //	string	是	机构类型1医院,2卫生院,3卫生所	1
        oname: '', //	string	是	机构名称	深圳宝安医院
        provinceId: '', //	string	是	省id	1
        provinceName: '', //	string	是	省名称	广东
        cityId: '', //	string	是	城市id	2
        cityName: '', //	string	是	城市名称	深圳
        areaId: '', //	string	是	区域id	3
        areaName: '', //	string	是	区域名称	宝安区
        streetId: '', //	string	是	街道ID	4
        streetName: '', //	string	是	街道名称	宝安街道
        communityId: '', //	string	是	社区id	6
        communityName: '', //	string	是	社区名称	宝安社区
        orgAddress: '', //	string	是	机构地址	广东省深圳市宝安区西乡街道2234号宝安中心医院
        contacts: '', //	string	是	联系人	老王儿子
        contactsMobile: '', //	string	是	联系人电话	15755996633
        enabled: USE, //	string	是	是否启动1开启2关闭	1
        oemail: '', //	string	是	邮箱	15755996633@163.com
        ologo: '', //	string	是	logo地址
        parentId: '', //	string	是		328572644813180928
        parentName: '' //	string	是
      },

      rules: {
        otype: { required: true, message: '请选择机构类别' },
        oname: [
          {
            required: true,
            message: '请输入机构名称',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule(30)
        ],
        parentId: { required: true, message: '请选择上级机构' },
        contactsMobile: {
          pattern: regPhone,
          message: '请输入正确的电话号码',
          trigger: 'blur'
        },
        orgAddress: { max: 50, message: '最多输入50个字符' },
        contacts: { max: 10, message: '最多输入10个字符' }
      },

      // 查看需要显示的数据
      dataInfo: {},

      // 弹框显/隐
      dialogIsShow: false,

      // 弹框类型(see:查看，insert:新增，update:修改)
      dialogType: '',

      // 列表loading
      loading: true,

      // 提交按钮的loading
      submitLoading: false
    }
  },

  created() {
    this.initStatic()
  },

  methods: {
    // 初始静态数据（不需要响应的数据）
    initStatic() {
      this.dialogTitles = { see: '查看', insert: '新增', update: '修改' }
      this.orgTypes = ['医院', '卫生院', '卫生所']
    },

    // 接收分页组件的数据
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },

    // 打开弹框
    openDialog(type, data) {
      this.getOrgList()
      this.getProvince()
      this.dialogIsShow = true
      this.dialogType = type
      if (type === 'insert') {
        // 新增手动清空表单
        Object.keys(this.formFields).forEach(key => {
          this.formFields[key] = ''
        })
        this.formFields.enabled = USE
      } else {
        this.dataInfo = data ? data : this.dataInfo
        Object.keys(this.formFields).forEach(key => {
          this.formFields[key] = this.dataInfo[key] === 0 ? '' : this.dataInfo[key]
        })
        this.formFields.sysGuid = this.dataInfo.sysGuid
        if (type === 'update') {
          Object.keys(rangeObj).forEach(key => {
            let { url, feildKey, paramKey } = rangeObj[key]
            if (this.dataInfo[feildKey]) {
              this.getRangeData(url, key, { [paramKey]: this.dataInfo[feildKey] })
            }
          })
        }
      }
      this.$nextTick(_ => {
        this.dialogType !== 'see' && this.$refs.form.clearValidate()
      })
    },

    // 提交新增和更新
    onSubmit() {
      this.$refs.form.validate(valid => {
        if (!valid) return
        this.submitLoading = true
        let url = `/sys/org/${this.dialogType}`
        let msg = this.dialogTitles[this.dialogType]
        this.$post(url, this.formFields)
          .then(res => {
            this.submitLoading = false
            this.dialogIsShow = false
            this.$refs.pagination.initPageList()
            this.$store.dispatch('getOrgList') // 重新获取机构
            this.$message.success(`${msg}成功`)
          })
          .catch(err => {
            this.submitLoading = false
            this.$message.error(err)
          })
      })
    },

    // 处理区域联动
    handleSelect(attr, val) {
      let { url, clearFeilds, clearData, paramKey } = rangeObj[attr]
      clearFeilds.forEach(t => {
        this.formFields[t] = ''
      })
      clearData.forEach(t => {
        this[t] = []
      })
      this.getRangeData(url, attr, { [paramKey]: val })
    },

    // 获取市、区、镇、村
    getRangeData(url, attr, params) {
      this.$post(`/dict/${url}`, params)
        .then(({ data }) => {
          this[`${attr}List`] = data
        })
        .catch(err => this.$message.error(err))
    },

    // 获取省
    getProvince() {
      if (this.provinceList.length) return
      this.$post('/dict/getProvince')
        .then(({ data }) => {
          this.provinceList = data
        })
        .catch(err => this.$message.error(err))
    },

    getOrgList() {
      if (this.parentOrgList.length) return
      this.$post('/sys/org/selectThisOrgAllChildOrg')
        .then(({ data }) => {
          this.parentOrgList = data
        })
        .catch(err => this.$message.error(err))
    }
  },

  computed: {
    regionAddress() {
      if (this.dialogType === 'see') {
        if (!this.dataInfo.provinceId) return ''
        let arr = ['provinceName', 'cityName', 'areaName', 'streetName', 'communityName'].map(
          item => (this.dataInfo[item] ? this.dataInfo[item] : '')
        )
        return arr.join('')
      }
    },
    btns() {
      let btnIds = this.$store.state.btnAuthorityIds
      return {
        hasInsert: ~btnIds.indexOf('106001001'), // 新增按钮权限
        hasUpdate: ~btnIds.indexOf('106001002'), // 修改按钮权限
        hasDel: ~btnIds.indexOf('106001003') // 删除按钮权限
      }
    }
  },

  watch: {
    // 机构类型改变
    // 'formFields.otype'(otype) {
    //   let fn = (data, parentId) => {
    //     this.parentOrgList = data ? data : []
    //     this.formFields.parentId = parentId ? parentId : ''
    //     this.$nextTick(() => {
    //       this.dialogType !== 'see' && this.$refs.form.clearValidate('parentId')
    //     })
    //   }
    //   if (!otype) return fn()
    // /sys/org/getThisOrgAndChild
    // this.$post('/sys/org/selectOrgName', { otype })
    //   .then(({ data }) => {
    //     let isClear = data.some(item => item.sysGuid === this.formFields.parentId)
    //     fn(data, isClear ? this.formFields.parentId : '')
    //   })
    //   .catch(err => this.$message.error(err))
    // }
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}

.table-box {
  padding: 0 20px;
  overflow: hidden;
}

.el-form.see-style {
  margin: 0 auto;
  max-width: 100%;
  .el-form-item {
    margin-bottom: 10px;
  }
}

.flex-item {
  & > .el-form-item {
    .flex-width(48%);
    &:nth-child(2n) {
      margin-left: auto;
    }
    &:last-child {
      .flex-width(100%);
    }
  }
}

.el-select.ctrl-height /deep/ .el-input {
  max-height: 32px;
}
</style>



